
































































































































    declare global {
       interface Window { PrismicJS: any, PrismicDOM: any} 
    }

	import { Component, Vue, Watch } from "vue-property-decorator";
	import { Route } from 'vue-router';
	import _Prismic from '@/prismic';
	
	Component.registerHooks(['beforeRouteEnter']);

	@Component
	export default class VideoStream extends Vue {
		prismicDOM = window.PrismicDOM; 	
        ministries: any[] = [];
		groups: any[] = [];
        current = {};
        interested = false;
        user: any = {name:'', email:''};
        overlay = 'linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),';
        middle_image: any = {};

		beforeRouteEnter(to: Route, from: Route, next: Function) {
			next((vm) => { vm._getPage(); });
		}
		
        async _getPage() {
			let api = await _Prismic();
		    let results = (await api.query(window.PrismicJS.Predicates.at('document.type', 'about'),
				{ fetch:[ 'about.ministries','about.connect_groups', 'about.middle_image' ]}
			)).results[0].data;
            
            this.ministries = results.ministries;
            this.groups = results.connect_groups;
            this.middle_image = results.middle_image;
		}

        showInterest(category, group_name) {
            this.current={ category, group_name}; 
            this.interested= true;
        }

        sendInterest() {
			if((this.user.name == '') || (this.user.email == '')) return;
			let connect = {...this.current, ...this.user };
            fetch("http://192.168.1.252:3000/contact-us", {
				method: 'post', body: JSON.stringify(connect),
				headers:{ 'Content-Type': 'application/json; charset=utf-8'}
			});
        }

        backgroundImage(url) {
            return this.overlay + 'url("'+ url +'")';
        }
}
